.all-posts{
  display: flex;
  font-family: -apple-system,'Open Sans',sans-serif;
  overflow: auto;
  height: 100%;
  color: #2C2F48FF;
  .postPreview {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;

    &__main{
      margin-bottom: 2px;
      background: #2c2f48;
      border-radius: 15px;
      margin-left: 10px;
      width: 100%;
      &-headerImg{
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: 0 0;
          border-top-right-radius: 12px;
          border-top-left-radius: 12px;
        }
      }
      &-info{
        display: flex;
        flex-direction: column;
        &-author{
          display: flex;
          align-items: center;
          padding-left: 40px;
          padding-top: 20px;
          color: #FFFFFF;
          .post-avatar{
            display: flex;
            align-items: center;
            justify-content: center;
          }
          .postAuthor-name{
            display: flex;
            flex-direction: column;
            span:first-child{
              font-size: 16px;
              font-weight: bold;
              line-height: 22px;
            }
            span:nth-child(2) {
              opacity: 0.6;
              font-size: 13px;
            }
          }
        }
        &-title{
          padding: 10px 40px;
          h1{
            font-size: 35px;
            font-weight: 600;
            text-align: center;
            color: #72ffff;
          }
        }
        &-tags{
          padding: 20px 40px;
          color: white;
          span {
            padding-right: 15px;
            font-weight: 600;
            opacity: 0.4;
          }
        }
        &-text{
          max-height: 300px;
          overflow: hidden;
          padding: 0 20px;

          .markdown {
            h3, h2, h1 {
              margin-left: 20px;
              color: #FFFFFF;
            }

            h1 {
              font-size: 28px;
            }
            ul{
              color:white;
            }

            pre {
              padding: 0 20px;
            }
          }
          p{
            font-size: 20px;
            line-height: 1.5;
            padding-left: 20px;
            padding-right: 20px;
            color: white;
          }
        }
        &-views{
          opacity: 0.6;
          padding: 20px 40px;
          display: flex;
          justify-content: space-between;
          color: #FFFFFF;
          .views{
            padding-right: 20px;
          }
          .comments{
          }
        }
      }
    }
    &__main:hover{
    }


  }
  .post__skeleton{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    &-content{
      width: 100%;
      background-color: #2C2F48FF;
      display: flex;
      flex-direction: column;
      border-radius: 20px;
      margin-bottom: 10px;
      .ant-skeleton{
        width: auto;
        &-image{
          width: auto;
          height: 400px;
          border-top-left-radius: 20px;
          border-top-right-radius: 20px;
        }
      }
      .ant-space{
        padding: 20px 10px 20px 40px ;
      }
      &-title {
        padding: 0 40px 40px;
        .ant-skeleton-content h3 {
          height: 28px;
          margin: 0 auto;
        }
      }
    }

  }
  .post-avatar img {
    width: 40px;
    height: 40px;
    margin-right: 10px;
    border-radius: 50%;
    object-fit: cover;
    object-position: 0 0;
  }
  .post-avatar {
    display: contents;
  }
}
.searchPost{
  display: flex;
  flex-direction: column;
  padding: 20px;
  margin-right: 2px;
  background-color: #2C2F48;
  border-radius: 15px;
  box-shadow: 15px 15px 25px rgba(0,0,0,0.2);
  color: #FFFFFF;
  &__navigation{
    display: flex;
    justify-content: center;
    padding:0 0 10px;
    .ant-segmented{
      width: 100%;
      &-item{
        color:white;
        &-label{
          padding: 0 4px;
        }
        &-selected{
          color: black;
        }
      }
      label:last-child{
        width: 0;
        overflow: hidden;
        flex: inherit;
      }
    }
    span{
      display: flex;
      flex-direction: column-reverse;
      width:100px;
      text-align: center;
      font-size: 16px;
      cursor: pointer;
    }
    span:hover{
      background: -webkit-linear-gradient(#d5adf1, #7b5cd3);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    span:hover:after{
      content: '';
      position: absolute;
      width: 91px;
      height: 3px;
      border-radius: 2px;
      background: #6f53b6;
    }

    .navigation-active {
      background: -webkit-linear-gradient(#d5adf1, #7b5cd3);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    .navigation-active:after{
      content: '';
      position: absolute;
      width: 91px;
      height: 3px;
      border-radius: 2px;
      background: #6f53b6;
    }
  }
  &__input{
    margin-bottom: 20px;
  }
  &__tagBlock {
    font-size: 22px;
    text-align: center;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    > span{
      color:#72ffff;
    }
    &-tags{
      height: 160px;
      margin-top: 10px;
      .tagList{
        margin: 0;
        padding: 0;
        display: inline-flex;
        flex-direction: column;
      }
      .tagList-item{
        color: #FFFFFF;
        opacity: 0.8;
        padding: 0 5px;
        transition: 0.5s;
        position: relative;
        list-style: none;
        border: 1px solid rgba(0,0,0,.1);
        font-size: 20px;
        cursor: pointer;
        z-index: 1;
      }
      .tagList-item:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 50%;
        background: #71fdfe;
        z-index: -1;
        transform-origin: right;
        transform: scaleX(0);
        transition: transform 0.5s;
      }
      .tagList-item:hover:before {
        transform-origin: left;
        transform: scaleX(1);
        transition: transform 0.5s;
      }
      .tagList-item:after{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 50%;
        background: #71fdfe;
        z-index: -1;
        transform-origin: left;
        transform: scaleX(0);
        transition: transform 0.5s;
      }
      .tagList-item:hover:after {
        transform-origin: right;
        transform: scaleX(1);
        transition: transform 0.5s;
      }
      .tagList-item:hover {
        color: black;
      }
    }
    &-tags li {
      margin: 0 auto;
    }
  }
  &__comments {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    > span{
      color:#72ffff;
    }
    .comments-description{
      font-size: 22px;
      margin-bottom: 10px;
    }
    &-list{
      width: 100%;
      a {
        color:#FFFFFF;
      }
      &-item{
        align-items: center;
        padding: 3px 8px 5px 8px;
        height: 60px;
        cursor: pointer;
        display: flex;
        margin-bottom: 8px;
        width: 100%;
        background: #393D5D;
        background-blend-mode: overlay;
        border-radius: 5px;
        box-shadow: 0 3px 8px rgba(0, 0, 0, .2);
        transition: 0.5s;

        .clip-comment-avatar{
          height: 44px;
          width: 44px;
          min-width: 44px;
          margin-right: 8px;
          clip-path: polygon(37% 5%, 61% 5%, 90% 26%, 94% 36%, 94% 66%, 91% 75%, 60% 97%, 38% 97%, 10% 76%, 5% 68%, 5% 35%, 11% 24%);
        }

        &-name{
          display: flex;
          flex-direction: column;
          height: 100%;
          width: 100%;
          overflow: hidden;
          .headerOf-comment{
            display: flex;
            justify-content: space-between;
            align-items: baseline;
            font-size: 13px;
            span:nth-child(2) {
              font-size: 11px;
            }
          }

          span:nth-child(2) {
            line-height: normal;
            font-size: 13px;
            color: rgba(255, 255, 255, 0.25);
          }
        }
      }
      .skeleton-comment{
        align-items: center;
        padding: 3px 8px 5px 8px;
        height: 60px;
        display: flex;
        width: 311px;
        justify-content: center;
        margin-bottom: 8px;
      }
    }
  }
}

